import { Component, OnInit, Input } from '@angular/core';
import { ICourseModel } from 'src/app/shared/Models/course-model';
import { CourseService } from 'src/app/shared/services/course-service/course.service';

@Component({
  selector: 'app-course-list',
  templateUrl: './course-list.component.html',
  styleUrls: ['./course-list.component.css']
})
export class CourseListComponent implements OnInit {

  @Input() public department = '';

  public CourseList: ICourseModel[] = [];
  public CourseDescription: string[] = [];

  closeResult: string;

  constructor(private courseService: CourseService ) { }
  
  getCoursesPerDepartment() {
    return this.courseService.getCourseForDepartment( this.department).then( courses => {
       this.CourseList = courses;
    });
  }

  ngOnChanges() {
    this.getCoursesPerDepartment();
  }

  ngOnInit() {
  }

}
