import { Component, OnInit } from '@angular/core';
import { ImageService } from 'src/app/shared/services/image-service/image.service';

@Component({
  selector: 'app-image-navigation',
  templateUrl: './image-navigation.component.html',
  styleUrls: ['./image-navigation.component.css']
})
export class ImageNavigationComponent implements OnInit {

  constructor(private imageService: ImageService) { }

  public imageCategories: any[] = [];
  getImageCategories(): any {
    return this.imageService.getImagesForCategory()
      .subscribe(response => this.imageCategories.push(response));
  }

  ngOnInit() {
    this.getImageCategories();
  }

}
