import { Injectable } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { User } from '../../Models/user.model';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  readonly rootUrl = 'https://localhost:44394/';
  constructor(private http: HttpClient, private fb: FormBuilder) { }

  formModel = this.fb.group({
    FirstName :[''],
    LastName :[''],
    Initials :[''],
    UserName :[''],
    Passwords: this.fb.group({
      Password:[''],
      ConfirmPassword :['']
    })
  });

  registerUser(user: User) {
    const body: User = {
      UserName: user.UserName,
      Password: user.Password,
      Email: user.Email,
      FirstName: user.FirstName,
      LastName: user.LastName
    }
    var reqHeader = new HttpHeaders({'No-Auth':'True'});
    return this.http.post(this.rootUrl + 'api/ApplicationUser/Register', body,{headers : reqHeader});
  }

  userAuthentication(formData) {
    return this.http.post(this.rootUrl + 'api/applicationUser/Login', formData);
  }

  getUserProfile() {
    var token = localStorage.getItem('token');
    var tokenHeader = new HttpHeaders({'Authorization':' Bearer '+ token})
    return this.http.get(this.rootUrl + 'api/UserProfile', {headers : tokenHeader });
  }

  roleMatch(allowedRoles): boolean {
    var isMatch = false;
    var payLoad = JSON.parse(window.atob(localStorage.getItem('token').split('.')[1]));
    var userRole = payLoad.role;
    allowedRoles.forEach(element => {
      if (userRole == element) {
        isMatch = true;
        return false;
      }
    });
    return isMatch;
  }

}
