import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ImageService } from 'src/app/shared/services/image-service/image.service';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {

  public category = '';
  ImageList = [];
  constructor(private route: ActivatedRoute,private router: Router, private imageService: ImageService) { }

  getImages(): any {
    this.imageService.getAllImages().subscribe(response => {
      this.ImageList = response;
    });
  }

  ngOnInit() {
    this.router.navigateByUrl('/images/all');
    this.route.params.subscribe(p => this.category = p['category']);
  }

}
