import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LayoutComponent } from './layout/layout.component';
import { FooterComponent } from './footer/footer.component';
import { ContactusComponent } from './contactus/contactus.component';
import { BannerComponent } from './banner/banner.component';
import { HeaderComponent } from './navigation/header/header.component';
import { SidenavListComponent } from './navigation/sidenav-list/sidenav-list.component';
import { AppRoutingModule } from './app-routing.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MaterialModule } from './material/material.module';
import { SignInComponent } from './user/sign-in/sign-in.component';
import { UserComponent } from './user/user.component';
import { SignUpComponent } from './user/sign-up/sign-up.component';
import { ToastrModule } from 'ngx-toastr';
import { UserService } from './shared/services/User-Service/user.service';
import { AboutusComponent } from './aboutus/aboutus.component';
import { GalleryComponent } from './tasc-images/gallery/gallery.component';
import { CoursesComponent } from './tasc-courses/courses/courses.component';
import { TeamComponent } from './team/team.component';
import { AuthInterceptor } from './auth/Interceptor/auth.interceptor';
import { CourseService } from './shared/services/course-service/course.service';
import { CourseNavigationComponent } from './tasc-courses/course-navigation/course-navigation.component';
import { CourseListComponent } from './tasc-courses/course-list/course-list.component';
import { ImageListComponent } from './tasc-images/image-list/image-list.component';
import { ImageNavigationComponent } from './tasc-images/image-navigation/image-navigation.component';
import { ImageService } from './shared/services/image-service/image.service';
import { ForbidenComponent } from './forbiden/forbiden.component';
import { AdminHomeModule } from './admin/admin-home/admin-home.module';
import { CustomerAdminLayoutModule } from './Admin-customer/layout/customer-admin-layout/customer-admin-layout.module';
import { CustomerProfileModule } from './Admin-customer/customer-profile/customer-profile.module';
import { AdminLayoutModule } from './admin/layout/admin-layout.module';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    LayoutComponent,
    FooterComponent,
    ContactusComponent,
    BannerComponent,
    HeaderComponent,
    SidenavListComponent,
    SignInComponent,
    UserComponent,
    SignUpComponent,
    AboutusComponent,
    GalleryComponent,
    CoursesComponent,
    TeamComponent,
    CourseNavigationComponent,
    CourseListComponent,
    ImageListComponent, 
    ImageNavigationComponent, ForbidenComponent
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule, MaterialModule, 
    FormsModule, ReactiveFormsModule, AppRoutingModule, FlexLayoutModule,

    AdminLayoutModule, AdminHomeModule,
    CustomerAdminLayoutModule, CustomerProfileModule,

    BrowserAnimationsModule, ToastrModule.forRoot({
      progressBar:true
    })
  ],
  providers: [ImageService, CourseService, UserService, {
                              provide: HTTP_INTERCEPTORS,
                              useClass: AuthInterceptor,
                              multi: true
                            }
              ],
  bootstrap: [AppComponent]
})
export class AppModule { }
