import { Component, OnInit, Input } from '@angular/core';
import { ImageModel } from 'src/app/shared/Models/image-model';
import { ImageService } from 'src/app/shared/services/image-service/image.service';

@Component({
  selector: 'app-image-list',
  templateUrl: './image-list.component.html',
  styleUrls: ['./image-list.component.css']
})
export class ImageListComponent implements OnInit {

  @Input() public category = '';

  public ImageList: ImageModel[] = []; 

  constructor(private imageService: ImageService) { }

  getImagesPerCategory() {
    return this.imageService.getImagesByCategory( this.category).then( images => {
       this.ImageList = images;
    });
  }

  ngOnChanges() {
    this.getImagesPerCategory();
  }

  ngOnInit() {
  }

}
