import { Routes, RouterModule } from "@angular/router";
import { CustomerMainLayoutComponent } from "../layout/customer-admin-layout/customer-main-layout/customer-main-layout.component";
import { CustomerProfileComponent } from "./customer-profile/customer-profile.component";
import { AuthGuard } from "src/app/auth/authGuard/auth.guard";
import { NgModule } from "@angular/core";

const routes: Routes = [
    { path: 'delegate', component: CustomerMainLayoutComponent,
        children: [
          { path: '', component: CustomerProfileComponent, canActivate:[AuthGuard], data :{ permittedRoles:['Customer'] }},
          { path: 'profile', component: CustomerProfileComponent, canActivate:[AuthGuard], data :{ permittedRoles:['Customer'] }}
        ]
    }
  ];

  @NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
  })

  export class CustomerProfileRoutingModule { }