import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/shared/services/User-Service/user.service';

@Component({
  selector: 'app-admin-mainlayout',
  templateUrl: './admin-mainlayout.component.html',
  styleUrls: ['./admin-mainlayout.component.css']
})
export class AdminMainlayoutComponent implements OnInit {

  userDetails:any;
  firstName:string;
  lastName:string;
  initials:string;

  constructor(private route: Router, private userService: UserService) { }

  ngOnInit() {
    this.getUserProfileDetails();    
  }

  private getUserProfileDetails() {
    this.userService.getUserProfile().subscribe(res => {
      this.userDetails = res;
      this.firstName = this.userDetails.firstName;
      this.lastName = this.userDetails.lastName;
      this.initials = this.userDetails.initials;

    }, err => {
      console.log(err);
    });
  }

  onLogout(){
    localStorage.removeItem('token');
    this.route.navigate(['./login'])
  }

}
