import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CourseService } from 'src/app/shared/services/course-service/course.service';

@Component({
  selector: 'app-course-navigation',
  templateUrl: './course-navigation.component.html',
  styleUrls: ['./course-navigation.component.css']
})
export class CourseNavigationComponent implements OnInit {

  public departments: any[] = [];
  navbarOpen = false;
  
  constructor(private service: CourseService) { }

  getDepartments(): any {
    return this.service.getCoursesForDepartment()
      .subscribe(response => this.departments.push(response));
  }

  toggleNavbar() {
    this.navbarOpen = !this.navbarOpen;
  }
  
  ngOnInit() {
    this.getDepartments();
  }

}
