import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { UserComponent } from './user/user.component';
import { SignUpComponent } from './user/sign-up/sign-up.component';
import { SignInComponent } from './user/sign-in/sign-in.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ContactusComponent } from './contactus/contactus.component';;
import { GalleryComponent } from './tasc-images/gallery/gallery.component';
import { CoursesComponent } from './tasc-courses/courses/courses.component';
import { TeamComponent } from './team/team.component';
import { AuthGuard } from './auth/authGuard/auth.guard';

const routes: Routes = [
  { path: '', component: HomeComponent, pathMatch: 'full' },
  { path: 'home', component: HomeComponent},
  { path: 'tascusers', redirectTo:'/login', pathMatch : 'full'},
  { path: 'aboutus', component: AboutusComponent },
  { path: 'courses', component: CoursesComponent },
  { path: 'courses/:department', component: CoursesComponent },
  { path: 'team', component: TeamComponent },
  { path: 'contactus', component: ContactusComponent },
  { path: 'images', component: GalleryComponent },
  { path: 'images/:category', component: GalleryComponent },
  { path: 'admin', redirectTo: 'admin/home', canActivate:[AuthGuard] },
  {
    path: 'signup', component: UserComponent,
    children: [{ path: '', component: SignUpComponent }]
  },
  {
      path: 'login', component: UserComponent,
      children: [{ path: '', component: SignInComponent }]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
